@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
:root {
  --primary-color: #17a2b8;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}
.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: #17a2b8;
  background: var(--primary-color);
  color: #fff;
}

.text-dark {
  color: green;
}

.itm {
  display: block;
  padding-left: 1rem;
  font-size: 1.17em;
  font-weight: bold;
  line-height: 2;
}

.profile-pic {
  position: relative;
  max-width: 130px;
  max-height: 130px;
  width: auto;
  height: auto;
  overflow: hidden;
  /* display: inline-block; */

  /* background-size: cover;  */
  margin: -31px;
  margin-left: -7px;
  border: 6px solid rgba(0, 77, 64, 0.4);
  border-radius: 50% !important;
}

.profile-pic img {
  width: 100%;
  height: auto;
  align-content: center;
}

.tracker {
  /* visibility: hidden; */
  position: fixed;
  /* margin-inline-end: auto;  */
  bottom: 0;
  right: 0;
}
.social {
  margin: 0 auto;
  width: auto;
  text-align: inherit;
  /* padding: 10px; */
}

.icon-btn {
  margin-left: auto;
  width: 50px;
  height: 50px;
  border: 0;
  color: #ffffff;
  cursor: pointer;
  font-size: 30px;
  line-height: 30px;
  border-radius: 30px;
  display: inline-block;
  margin: 4px;
}

.github {
  color: black;
}

.linkedin {
  color: #0077b5;
}

.angellist {
  color: grey;
}

.icon-btn:hover {
  color: #ffffff;
}

.linkedin:hover {
  background-color: #0077b5;
}

.angellist:hover {
  background-color: grey;
}

.github:hover {
  background-color: black;
}

/* Buttons - Read Me, Contact Us */

.readme {
  position: relative;
  padding: 20px;
  background: #fff;
  color: #0c8cbe;
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 1px;
  display: inline-block;
}

.contactme {
  position: relative;
  padding: 11px 35px;
  background: none;
  color: #fff;
  border: 2px solid #fff;
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 1px;
  display: inline-block;
  margin-top: 1.5em;
  border-radius: 5px;
  -webkit-border-radius: 5px;
}

.contactme:hover {
  color: #00796b;
  background-color: white;
}
